import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CovidTrackerComponent } from './covid-tracker/covid-tracker.component';
import {PhotosComponent} from './photos/photos.component';
import {DistrictwisetrackerComponent} from './districtwisetracker/districtwisetracker.component'

const routes: Routes = [
  { path: '', component: CovidTrackerComponent },
  { path: 'district', component: DistrictwisetrackerComponent },
  { path: 'photos/:albumId', component: PhotosComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
