import { Component, OnInit } from '@angular/core';
import {ServiceService} from '../services/service.service';

@Component({
  selector: 'app-covid-tracker',
  templateUrl: './covid-tracker.component.html',
  styleUrls: ['./covid-tracker.component.css']
})
export class CovidTrackerComponent implements OnInit {

  covidData;
  public covidDataStatewise:any=[];
  public covidDataDistrictwise:any=[];
  public covidDistrictData:any=[];
  public isStatewiseData : boolean=true;
  public state:any='';
  
  // Doughnut
  public doughnutChartLabels: string[] = ['Download Sales', 'In-Store Sales', 'Mail-Order Sales'];
  public doughnutChartData: number[] = [350, 450, 100];
  public doughnutChartType = 'doughnut';
  
  constructor(private covidService: ServiceService,) { }

  ngOnInit() {
    //this.covidData = this.covidService.getcovidData();
    console.log(this.covidData); 
    this.getCovid2019Data();   
  }

  
  getCovid2019Data(){
    this.covidService.get('data.json').then((response: any) => {
      this.covidData = response;
      this.covidDataStatewise=response.statewise;
      console.log(this.covidDataStatewise.lenght);
    });
  }

  getStateData(state){
    //alert(state);
    this.state=state;
    this.isStatewiseData=false;
    this.covidService.get('state_district_wise.json').then((response: any) => {
      var distData = response[state].districtData;
      this.covidDataDistrictwise=response[state].districtData;
      console.log(this.covidDataDistrictwise.length);
      let evilResponseProps = Object.keys(distData);
      console.log(evilResponseProps);
      let goodResponse = [];
      // Step 3. Iterate throw all keys.
      var keyvalue 
      for (var prop in evilResponseProps) { 
        //goodResponse.push(evilResponseProps[prop]);
        keyvalue = evilResponseProps[prop]
        this.covidDataDistrictwise[keyvalue]['district']=evilResponseProps[prop];        
      }          
      for(var i=0; i<evilResponseProps.length;i++)
      { 
        keyvalue = evilResponseProps[i];       
        this.covidDistrictData.push(this.covidDataDistrictwise[keyvalue]);
        //arr.push
      }
      console.log(this.covidDataDistrictwise);
      console.log(this.covidDistrictData);
    });
  }

}
